import React from "react";
import classes from "./LinksInsideSidebar.module.scss"
import {NavLink} from "react-router-dom";


const LinksInsideSidebar = props => {
	return (
		<nav className={classes.LinksInsideSidebar}>
			<li>
				<NavLink to={"/"}> Главная </NavLink>
			</li>
			<li>
				<NavLink to={"/depo"}> Пополнения </NavLink>
			</li>
		</nav>
	)
};

export default LinksInsideSidebar;