import React from "react";
import classes from './Main.module.scss'
import CompaniesList from "../../components/CompaniesList/CompaniesList";
import {Title} from "../../components/Title/Title";
import {Button} from "../../components/UI/Button/Button";
import {Modal} from "../../hoc/Modal/Modal";
import {Input} from "../../components/UI/Input/Input";
import {FadeBg} from "../../hoc/Modal/FadeBg";
import axios from '../../axios/axios-settings'

export default class Main extends React.Component {
	state = {
		flag: true,
		isOpenModal: false,
		companyName: '',
		count: '',
		dividendPerShare: '',
		company: {}
	};

	showModalAndAddCompany = () => {
		this.setState({
			isOpenModal: true,
		})
	};

	closeModalHandler = () => {
		this.setState({
			isOpenModal: false,
		})
	};

	mainModalFormAddCompany = async (e) => {
		e.preventDefault();

		try {
			// TODO: создать общую переменную UserKey, которая будет определять пользователя
			// Ключ (ID пользователя)
			const userKey = "aHmcqjPPBwXvW397FPL31TtkwfJ3";
			let company = Object.assign({}, this.state.company);

			const {companyName, count, dividendPerShare} = this.state;

			company = this.createCompany(companyName, count, dividendPerShare);

			const response = await axios.put(`/${userKey}/portfolio/companies.json`, company);

			if (response.status === 200) {
				this.setState({
					company,
					isOpenModal: false,
					flag: true
				})
			}

		} catch (e) {
			console.error(e)
		}
	};

	createCompany = (name, count, dividendPerShare) => {
		return {
			[name]: {
				count: count,
				dividendPerShare: dividendPerShare
			}
		}
	};

	getCompanyName = (e) => {
		this.setState({
			companyName: e.target.value
		})
	};

	getShareCount = (e) => {
		this.setState({
			count: e.target.value
		})
	};

	getDividendPerShare = (e) => {
		this.setState({
			dividendPerShare: e.target.value
		})
	};

	render() {
		return (
			<div className={classes.Main}>
				<Title title="Список компаний"/>

				<table>
					<thead>
					<tr>
						<th>Компания</th>
						<th>Количество акций</th>
						<th>Дивиденды</th>
						<th>Итого</th>
					</tr>
					</thead>

					{
						this.state.flag ? <CompaniesList/> : null
					}

				</table>


				<div className={classes.addSection}>
					<Button
						classses={["green"]}
						name="Добавить компанию"
						onClick={this.showModalAndAddCompany}
					/>
				</div>

				{
					this.state.isOpenModal
						?
						<React.Fragment>
							<Modal classes={[this.state.modalClass]}>
								<h3 className={classes.MainModalTitle}>Добавить компанию</h3>

								<form onSubmit={this.mainModalFormAddCompany}>
									<Input onChange={this.getCompanyName} label={"Имя компании"}/>
									<Input onChange={this.getShareCount} label={"Количество акций"}/>
									<Input onChange={this.getDividendPerShare} label={"Размер дивиденда (в год)"}/>


									<Button
										classses={["blue"]}
										name="Добавить компанию"
										onClick={this.showModalAndAddCompany}
									/>
								</form>
							</Modal>
							<FadeBg onClick={this.closeModalHandler}/>
						</React.Fragment>
						: null
				}

			</div>
		)
	}
}