import React from "react";
import classes from "./CompaniesList.module.scss";
import axios from '../../axios/axios-settings'

class CompaniesList extends React.Component {
	state = {
		companies: {},
		beforeTax: '',
		afterTax: ''
	};

	countDividends = (companies) => {
		let beforeTax = 0;
		let afterTax = 0;
		// Суммирую

		for (let name in companies) {
			beforeTax += (companies[name].count * companies[name].dividendPerShare);
		}
		// Округляю
		beforeTax = Math.round(beforeTax * 100) / 100;
		// Вычисляю читый доход без налогов и округляю
		afterTax = Math.round((beforeTax - (beforeTax * 0.13)) * 100) / 100;
		// озвращаю объект
		return {
			beforeTax, afterTax
		}
	};

	// Метод для вывода компаний на страницу
	renderCompanies = (companies) => {
		const companyArr = [];
		let key = Math.random();

		for (let name in companies) {
			const arrItem = (
				<tr key={key}>
					<td>{name}</td>
					<td>{companies[name].dividendPerShare}</td>
					<td>{companies[name].count}</td>
					<td>{companies[name].count * companies[name].dividendPerShare}</td>
				</tr>
			);

			companyArr.push(arrItem);
			key += Math.random();
		}

		return companyArr
	};

	async componentDidMount() {
		// TODO: создать общую переменную UserKey, которая будет определять пользователя
		// сейчас это aHmcqjPPBwXvW397FPL31TtkwfJ3
		try {
			// Ключ (ID пользователя)
			const userKey = "aHmcqjPPBwXvW397FPL31TtkwfJ3";
			// Импорт портфеля пользователя
			const response = await axios.get(`/${userKey}.json`);
			// Импорт компаний
			const {companies} = response.data.portfolio;
			const {beforeTax, afterTax} = this.countDividends(companies);

			this.setState({companies, beforeTax, afterTax});

		} catch (e) {
			console.error(e)
		}
	}

	render() {
		return (
			<React.Fragment>
				<tbody>
				{
					this.renderCompanies(this.state.companies)
				}
				</tbody>

				{
					<tfoot>
					<tr>
						<td colSpan={3}>
							Прибыль до вычета налогов
						</td>
						<td>
							{this.state.beforeTax}
						</td>
					</tr>

					<tr>
						<td colSpan={3}>
							Прибыль после вычета налогов
						</td>
						<td className={classes.afterTax}>
							{this.state.afterTax}
						</td>
					</tr>
					</tfoot>
				}
			</React.Fragment>
		)
	}
}

export default CompaniesList;
