import React from "react";
import classes from './DepoCurrency.module.scss'
import DepoDateAndAmount from "../DepoDateAndAmount/DepoDateAndAmount";


const DepoCurrency = props => {
	const currencyArr = props.depo[props.year];

	return (
		<React.Fragment>
			{
				currencyArr.map((obj, index) => {
					// ключ - это имя объекта (валюта)
					const key = Object.keys(obj);

					return (
						<div key={index} className={classes.DepoCurrency}>
							<h4 className={classes.name}>{key[0] === 'rub' ? "Рубли" : key[0] === "usd" ? "Доллары" : null}</h4>

							<ul>
								<DepoDateAndAmount currency={key} depo={obj[key]}/>
							</ul>
						</div>
					)
				})
			}
		</React.Fragment>

	)
};

export default DepoCurrency;