import React from "react";
import classes from './DepoYear.module.scss'
import DepoInnerWrapper from "../../container/DepoWrapper/DepoInnerWrapper";
import axios from "../../axios/axios-settings";

class DepoYear extends React.Component {
	state = {
		deposit: []
	};

	async componentDidMount() {
		// TODO: создать общую переменную UserKey, которая будет определять пользователя
		// сейчас это aHmcqjPPBwXvW397FPL31TtkwfJ3
		try {
			// Ключ (ID пользователя)
			const userKey = "aHmcqjPPBwXvW397FPL31TtkwfJ3";
			// Импорт портфеля пользователя
			const response = await axios.get(`/${userKey}.json`);
			// Импорт компаний
			const {deposit} = response.data.portfolio;

			this.setState({deposit})
		} catch (e) {
			console.error(e)
		}
	}

	render() {
		return (
			<div className={classes.DepoYear}>
				{
					this.state.deposit.map((obj, index) => {
						const year = Object.keys(obj);
						return <DepoInnerWrapper year={year} index={index} key={index}/>
					})
				}
			</div>
		)
	}
};

export default DepoYear;