import React from "react";
import classes from './DepoDateAndAmount.module.scss'

const DepoDateAndAmount = props => {
	const dep = props.depo;

	// Сознание нового массива дата-сумма
	const newDep = dep.sort((a, b) => new Date(a.date) - new Date(b.date));

	// Получение правильного формата даты и времени
	const getDate = date => {
		const day = new Date(date).getDate();
		let month = new Date(date).toLocaleDateString("ru", {month: 'long',});

		month === 'март' ? month += "a" : month = month.slice(0, month.length - 1) + "я";

		return [day, month].join(' ');
	};

	return (
		<React.Fragment>
			{
				newDep.map((obj, index) => {
					return (
						<li className={classes.li} key={index}>
							<span>
								{getDate(obj.date)}
							</span>

							<span>
								{obj.amount.toLocaleString("ru")} {props.currency === 'rub' ? "руб" : props.currency === 'usd' ? "$" : null}
							</span>
						</li>
					)
				})
			}
		</React.Fragment>

	)
};

export default DepoDateAndAmount;