import React from "react";
import classes from "./DepoInnerWrapper.module.scss";
import DepoCurrency from "../../components/DepoCurrency/DepoCurrency";
import {users} from "../../base";

const arrYears = users.aHmcqjPPBwXvW397FPL31TtkwfJ3.portfolio.deposit;

export default class DepoInnerWrapper extends React.Component {
  state = {
	isOpen: false
  };

  showMoreHandler = () => {
	this.setState({
	  isOpen: !this.state.isOpen
	});
  };

  render() {
	return (
		<div>
		  <p onClick={this.showMoreHandler} className={classes.year}>
			{this.props.year} год
		  </p>

		  <div className={classes.currency}>
			{this.state.isOpen
				? (
					<DepoCurrency
						depo={arrYears[this.props.index]}
						year={this.props.year}
					/>
				) : null}
		  </div>
		</div>
	);
  }
}
