import React from "react";
import classes from './Deposit.module.scss'
import DepoYear from "../../components/DepoYear/DepoYear";


export default class Deposit extends React.Component {
  render() {
	return (
		<div className={classes.Deposit}>

		  {/* Форма ввода пополнения */}
		  <div>
			<h3 className={classes.infoTitle}>
			  Добавить пополнение
			</h3>

			<div style={{margin: "15px 0", padding: "0 15px"}}>
			  <form>
				<input type="text" placeholder={'Введите сумму'}/>
				<input type="date" min="2000-01-01"/>
				<button>
				  Добавить пополнение
				</button>
			  </form>
			</div>

		  </div>

		  {/*Общий контейнер для пополнения и статистики*/}
		  <div className={classes.depoAndStatistic}>
			{/*Пополнения по годам*/}
			<h3 className={classes.infoTitle}>Общая информация по пополнениям</h3>

			<DepoYear/>

			{/* Статистика */}
			<div>
			  <p>
				Тут планируется блок статистики
			  </p>

			  <div>
				<h5>Всего пополнений:</h5>
				<ul>
				  <li>Рубли: 300 000 руб</li>
				  <li>Доллары: 41 $</li>
				</ul>
			  </div>

			</div>
		  </div>

		</div>
	)
  }
}